$start: 1;
$end: 100;
$step: 1;
.w{
	@for $i from $start through $end {
		&#{$i * $step}p{
			width: percentage($i * $step / 100) !important;

		}
	}
}
.mw{
	@for $i from $start through $end {
		&#{$i * $step}p{
			max-width: percentage($i * $step / 100) !important;

		}
	}
}
